@import url(https://fonts.googleapis.com/css?family=Dosis);
* {
	box-sizing: border-box;
	color: rgb(0, 0, 0);
	font-family: 'Dosis', sans-serif;
	font-size: 16pt;
	font-weight: 300;
	border: 0;
}

body {
	padding: 0;
	margin: 0;
}

main {
	display: grid;
	height: 100vh;
	width: 100vw;
	grid-template-columns: 1rem 2fr 1rem 1.5fr 1rem;
	grid-template-rows: 1rem 1fr 1rem;
	grid-template-areas:
		". . . . ."
		". select-menu-container . check-container ."
		". . . . .";
	background: rgb(171, 169, 169);
}
.check-container {
	grid-area: check-container;
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 4rem 0.5rem 1fr;
	grid-template-areas:
		"table-id"
		"."
		"table-order";
	padding: 0.5rem;
	background-color: rgb(194, 194, 194);
	border-radius: 0.25rem;
	overflow: hidden;
}
.table-order {
	grid-area: table-order;
	display: grid;
	grid-template-rows: auto 0.5rem 3rem 0.5rem 2rem 1rem 4rem;
	grid-template-areas:
		"show-ordered-item"
		"."
		"added-notes"
		"."
		"total-check"
		"."
		"send-order";
	overflow: hidden;
}

.show-ordered-item {
	grid-area: show-ordered-item;
	padding-right: 0.5rem;
	overflow-y: scroll;
}

.added-notes{
	grid-area: added-notes;
	display: block;
	overflow-y: scroll;
}

.ordered-item {
	display: block;
	grid-area: ordered-item;
	padding: 0.5rem;
	cursor: pointer;
	border-radius: 0.25rem;
	background: rgb(183, 218, 162);
	margin-bottom: 0.5rem;
}
.total-check {
	display: flex;
	grid-area: total-check;
	border-radius: 0.25rem;
	background-color: rgb(171, 171, 171);
	padding: 0.5rem;
	margin: 0.25rem;
	align-items: center;
}
.send-order {
	display: flex;
	grid-area: send-order;
	background-color: rgb(218, 162, 172);
	border-radius: 0.25rem;
	font-weight: bold;
	align-items: center;
	justify-content: center;
}
.added-note{
	font-size: 12pt;
	border-radius: 0.25rem;
	background-color: rgb(186, 186, 186);
	padding: 0.5rem;
	margin: 0.25rem;
	align-items: center;
}
.table-id {
	display: flex;
	grid-area: table-id;
	border-radius: 0.25rem;
	background-color: rgb(230, 224, 165);
	align-items: center;
	justify-content: center;
}

.select-menu-container {
	display: grid;
	grid-area: select-menu-container;
	grid-template-columns: 1fr;
	grid-template-rows: 4rem 0.5rem 1fr 0.5rem auto;
	grid-template-areas:
		"meal-type"
		"."
		"menu-items"
		"."
		"dashboard";
	background-color: rgb(194, 194, 194);
	border-radius: 0.25rem;
	padding: 0.5rem;
}

.menu-items {
	grid-area: menu-items;
	display: block;
}

.menu-items {
	display: block;
	grid-area: menu-items;
	overflow-y: scroll;
}
.item {
	display: block;
	padding: 0.5rem;
	cursor: pointer;
	border-radius: 0.25rem;
	background: rgb(183, 218, 162);
	margin-bottom: 0.5rem;
}
.meal-type {
	grid-area: meal-type;
	display: grid;
	grid-template-columns: 1fr 0.5rem 1fr 0.5rem 1fr;
	grid-template-areas:
		'breakfast-button . meals-button . beverages-button';
}
.breakfast {
	grid-area: breakfast-button;
	display: flex;
	background: rgb(162, 215, 218);
	border-radius: 0.25rem;
	border: 0;
	align-items: center;
	justify-content: center;

}

.meals{
	grid-area: meals-button;
	display: flex;
	background: rgb(173, 162, 218);
	border-radius: 0.25rem;
	border: 0;
	align-items: center;
    justify-content: center;
}

.beverages {
	grid-area: beverages-button;
	display: flex;
	background: rgb(218, 162, 216);
	border-radius: 0.25rem;
	border: 0;
	align-items: center;
	justify-content: center;

}
.dashboard {
    grid-area: dashboard;
    display: grid;
    grid-template-columns: 1fr 1rem 1fr;
    grid-template-rows: 3rem 1rem 4rem;
    grid-template-areas:
    "notes notes notes"
    ". . ."
    "home-button . send-note";
}
.notes {
	display: flex;
	grid-area: notes;
	padding: 0.25rem;
	border-radius: 0.25rem;
	font-size: 12pt;
	outline: none;
	overflow: hidden;
	overflow-wrap: break-word;
	overflow-y: scroll;
	resize: none;
	text-overflow: clip;
	background-color: rgb(186, 186, 186);
}
.home-button {
    grid-area: home-button;
    display: flex;
	background: rgb(218,162,192);
	border-radius: 0.25rem;
	border: 0;
	align-items: center;
    justify-content: center;
}
.send-note {
    grid-area: send-note;
    display: flex;
	background: rgb(162, 178, 218);
	border-radius: 0.25rem;
	border: 0;
	align-items: center;
    justify-content: center;
}
